module.exports = ({ stores, services }) => () => {

    const $intro = document.createElement('div');
    $intro.className = 'intro';

    $intro.innerHTML = `          
        <strong>Drag & drop images</strong> <span class="or"></span>
        <strong><a class="choose-images">Choose images</a></strong> <span class="or"></span>
        <strong><a class="import-gravatar">Import images from Gravatar</a></strong>    
    `;

    const $fileUpload = document.createElement('input');
    $fileUpload.type = 'file';
    $fileUpload.multiple = true;
    $fileUpload.accept = 'image/*';
    $fileUpload.style.display = 'none';
    $intro.append($fileUpload);

    const $chooseImages = $intro.querySelector('.choose-images');
    $chooseImages.addEventListener('click', e => {
        e.preventDefault();
        $fileUpload.click();
    });

    $fileUpload.addEventListener('change', e => {
        Array.from(e.target.files).forEach(services.tags.insertImage());
    });

    const $gravatar = $intro.querySelector('.import-gravatar');

    $gravatar.addEventListener('click', () => {
        stores.app.setState({ modal: 'gravatar' });
    });

    return $intro;

};



