








module.exports = (modules, wiring, initial = {}) => {
    return Object.entries(wiring).reduce((acc, [key, resolver]) => {
        const pipeline = getPipeline(acc, key, modules);
        return pipe(pipeline, resolver);
    }, initial);
};

const getPipeline = (acc, key, graph) => [
    str => {
        if (!isString(str)) return;
        return str.split(/[ ,;]/).map(key => key.trim());
    },
    arr => {
        if (!Array.isArray(arr)) return;
        return deps => {
            const partials = arr.map(key => ({ [key]: deps[key] }));
            return Object.assign(...partials);
        }
    },
    fun => {
        // console.log(`Wiring ${key}...`);

        const product = {};
        const deps = { ...fun(acc), [key]: product };
        const val = graph[key];
        const private = isFunction(val) ? val(deps) : deepMap(val, deps, product);

        const public = removePrivates(private, {});

        return Object.assign(acc, { [key]: public });
    }
];

const deepMap = (obj, deps, product) => {
    return Object.entries(obj).reduce((acc, [key, val]) => {

        // if (key === 'propagateRoleName') {
        // console.log(product)
        // }

        if (key[0] === '__') {
            return acc;
        }

        const isPrivate = key[0] === '_';

        key = key[0] === '_' ? key.substring(1) : key;

        const newVal = isFunction(val) ? val(deps) : deepMap(val, deps, {});

        if (isPrivate) newVal.isPrivate = isPrivate;


        // if (excludePrivate && isPrivate) return acc;

        return Object.assign(acc, { [key]: newVal });
    }, product);
};

const removePrivates = (obj, product) => {
    // return obj

    if (!isPlainObject(obj)) return obj; //for "start"

    return Object.entries(obj).reduce((acc, [key, val]) => {

        if (val.isPrivate)
            return acc;

        const newVal = isPlainObject(val) ? removePrivates(val, {}) : val;

        return Object.assign(acc, { [key]: newVal });
    }, product);
};


