module.exports = ({ gravatar, stores, services, URL }) => () => {

    const { freetext, fallback } = stores.gravatar.getState();

    const emails = freetext.split(/[\r\n,;]+/g).map(s => s.trim()).filter(s => s);

    emails.forEach(async email => {
        const { name, imageBlob } = await gravatar.fetchNameAndImage(email, fallback);
        const image = URL.createObjectURL(imageBlob);
        services.tags.insertTag({ name, image });
    });

    stores.gravatar.setState({ freetext: '' });

};
